import { Component, ViewContainerRef } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { Globals } from './globals';
// import { ModalDialogModule } from 'ngx-modal-dialog';
// import { IModalDialog, IModalDialogOptions,IModalDialogButton, ModalDialogService, SimpleModalComponent } from 'ngx-modal-dialog';
// import { ModalAboutComponent } from './modal-about/modal-about.component';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  ip: any;
  interface: any;
  main_form2: boolean;
  update_form2: boolean;
  mainIDD: any;
  submemberData: any;
  hostname: any;
  loopback: any;
  ID: any;
  main_form: boolean;
  update_form: boolean;
  memberData: any;
  serverurl: string;
  headers: any;
  title = 'myApp';
  p: number = 1;
  constructor(private http: Http,private spinnerService: Ng4LoadingSpinnerService,private globals: Globals,private viewRef: ViewContainerRef) {
    this.serverurl = globals.serverUrl;
   }

   ngOnInit() {  
    this.spinnerService.show();
    this.update_form = false;
    this.main_form = true;
    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded'});
    let options = new RequestOptions({ headers: headers });

    this.http.get(this.serverurl+"view.php", { headers: this.headers })
        .subscribe((res: Response) => {
          var response = res.json();                              
          this.memberData = response;
      })

  }
  getallrecord(){
    this.http.get(this.serverurl+"view.php", { headers: this.headers })
        .subscribe((res: Response) => {
          var response = res.json();                              
          this.memberData = response;
      })
  }

  submit(data){    
    // let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded'});
    // let options = new RequestOptions({ headers: headers });
    this.spinnerService.show();
    var body = JSON.stringify(data);
    //this.isClick= false;         

    this.http.post(this.serverurl+"add.php", body, { headers: this.headers })
        .subscribe((res: Response) => {
          var response = res.json();                              
            if(response.STATUS == "Success"){ 
                this.spinnerService.hide();
                this.getallrecord();               
                //this.isClick= true;
                alert("Data added successfully");
            }else{

            }
      })
  }
  edit(ID){    
    console.log("kk");
    this.update_form = true;
    this.main_form = false;
    var data = { ID : ID};
    this.spinnerService.show();

    this.http.post(this.serverurl+"view_by_id.php",JSON.stringify(data), { headers: this.headers })
                .subscribe((res: Response) => {
                  this.spinnerService.hide();
                  var response = res.json();       
                  this.hostname = response[0].hostname;
                  this.loopback = response[0].loopback;
                  this.ID = response[0].ID;                     
                  
      })    
  }

  delete(ID){
    var data = {ID:ID};
    //this.isClick = false;
    this.spinnerService.show();

    this.http.post(this.serverurl+"delete.php",JSON.stringify(data), { headers: this.headers })
                .subscribe((res: Response) => {
                  this.spinnerService.hide();
                  //this.isClick = true;
                  var response = res.json();      
                  if(response.STATUS == "Success"){
                    alert("Data deleted successfully");
                    this.getallrecord();
                  }else{
                    alert("Error in deleting data");
                  }
      })    
  }

  update(userdata){
    let body = JSON.stringify(userdata);
    //this.isClick = false;
    this.spinnerService.show();

    this.http.post(this.serverurl+"update.php",body, { headers: this.headers })
                .subscribe((res: Response) => {
                  this.spinnerService.hide();
                  //this.isClick = true;
                  var response = res.json();      
                  if(response.STATUS == "Success"){
                    alert("Data updated successfully");
                    window.location.reload();
                    //this.update_form = false;
                    //this.main_form = true;
                  }else{
                    alert("Error in updating data");
                  }
      })    
  }
  open(ID){    
    this.mainIDD = ID;
    this.spinnerService.show();  
      this.update_form2 = false;
      this.main_form2 = true;
      var mainID = ID;
      var body = {mainID : mainID};  

      this.http.post(this.serverurl+"view_sub_by_id.php",JSON.stringify(body), { headers: this.headers })
          .subscribe((res: Response) => {
            this.spinnerService.hide();
            var response = res.json();                       
            this.submemberData = response;
        })    
    }
    add_sub(data){    
      event.preventDefault();
      window.stop()
      // let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded'});
      // let options = new RequestOptions({ headers: headers });
      this.spinnerService.show();
      var body = JSON.stringify(data);
      //this.isClick= false;         
  
      this.http.post(this.serverurl+"add_sub.php", body, { headers: this.headers })
          .subscribe((res: Response) => {
            var response = res.json();                              
              if(response.STATUS == "Success"){ 
                  this.spinnerService.hide();
                  this.getsuballrecord();               
                  //this.isClick= true;
                  alert("Data added successfully");
              }else{
                this.spinnerService.hide();
                alert("Error in adding details");
              }
        })
    }
    getsuballrecord(){
      this.spinnerService.show();  
      var mainID = this.mainIDD;
      var body = {mainID : mainID};  

      this.http.post(this.serverurl+"view_sub_by_id.php",JSON.stringify(body), { headers: this.headers })
          .subscribe((res: Response) => {
            var response = res.json();                              
            this.submemberData = response;
        })
    }
    edit_sub(ID){    
      console.log("kk");
      this.update_form2 = true;
      this.main_form2 = false;
      var data = { ID : ID};
      this.spinnerService.show();
  
      this.http.post(this.serverurl+"view_sub_by_id_edit.php",JSON.stringify(data), { headers: this.headers })
                  .subscribe((res: Response) => {
                    this.spinnerService.hide();
                    var response = res.json();       
                    this.interface = response[0].interface;
                    this.ip = response[0].ip;
                    this.ID = response[0].ID;                     
                    
        })    
    }
  
    delete_sub(ID){
      event.preventDefault();
      window.stop()
      var data = {ID:ID};
      //this.isClick = false;
      this.spinnerService.show();
  
      this.http.post(this.serverurl+"delete_sub.php",JSON.stringify(data), { headers: this.headers })
                  .subscribe((res: Response) => {
                    this.spinnerService.hide();
                    //this.isClick = true;
                    var response = res.json();      
                    if(response.STATUS == "Success"){
                      alert("Data deleted successfully");
                      this.getsuballrecord();
                    }else{
                      alert("Error in deleting data");
                    }
        })    
    }
  
    update_sub(userdata){
      event.preventDefault();
      window.stop()
      let body = JSON.stringify(userdata);
      //this.isClick = false;
      this.spinnerService.show();
  
      this.http.post(this.serverurl+"update_sub.php",body, { headers: this.headers })
                  .subscribe((res: Response) => {
                    this.spinnerService.hide();
                    //this.isClick = true;
                    var response = res.json();      
                    if(response.STATUS == "Success"){
                      alert("Data updated successfully");
                      this.update_form2 = false;
                      this.main_form2 = true;
                      this.getsuballrecord();
                    }else{
                      alert("Error in updating data");
                    }
        })    
    }

  cancel(){
    this.update_form = false;
    this.main_form = true;
  }
  cancel2(){
    this.update_form2 = false;
    this.main_form2 = true;
  }
}
